.toggle {
  display: block;
  width: 23px;
  height: 17px;
  margin-top: 2px;
  pointer-events: none;
}

.toggle span {
  position: relative;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: all 0.15s;
  backface-visibility: hidden;
  border-radius: 2px;
}

.toggle span:after, .toggle span:before {
  content: ' ';
  position: absolute;
  left: 0;
  top: 7px;
}

.toggle span:after {
  top: 14px;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 2px;
}

.toggle span:before {
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 2px;
}

.toggle.is-active {
  pointer-events: auto;
}

.toggle.is-active span {
  background-color: transparent;
}

.toggle.is-active span:before {
  transform: rotate(45deg) translate(-2px, 2px);
}

.toggle.is-active span:after {
  transform: rotate(-45deg) translate(3px, -7px);
}

