.hero-slider {
  position: relative;
}

.hero-slider__slides {
  width: 100%;
  min-height: 320px;
  position: relative;
}

.hero-slider__slide {
  position: absolute;
  top: auto;
  bottom: 0;
  left: 0;
  background-color: #fff;
  width: 100%;
  min-height: 320px;
  border-radius: 4px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  transform-origin: center;
  transition: 0.3s cubic-bezier(0.28, 0.55, 0.385, 1.65);
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.hero-slider__slide.is-active .hero-slider__desc {
  transform: translateX(0);
  opacity: 1;
}

.hero-slider__slide.is-active .hero-slider__img {
  transform: translateY(0);
  opacity: 1;
}

.hero-slider__slide_inner {
  opacity: 1;
  width: 100%;
  padding: 0 120px 0 70px;
  display: flex;
  transition: opacity .35s linear;
  transition-delay: .2s;
}

@media screen and (max-width: 768px) {
  .hero-slider__slide_inner {
    padding: 0 50px 0 70px;
  }
}

.hero-slider__slide:not(:first-child) {
  background-image: none !important;
}

.hero-slider__slide:not(:first-child) .hero-slider__slide_inner {
  opacity: 0;
}

.hero-slider__slide:nth-child(1) {
  z-index: 10;
  bottom: 0;
  transform-origin: bottom;
  transform: scale(1);
}

.hero-slider__slide:nth-child(2) {
  z-index: 9;
  bottom: -15px;
  transform-origin: bottom;
  transform: scale(0.97);
}

.hero-slider__slide:nth-child(3) {
  z-index: 8;
  bottom: -30px;
  transform-origin: bottom;
  transform: scale(0.94);
}

.hero-slider__slide:nth-child(4) {
  z-index: 7;
  bottom: -45px;
  transform-origin: bottom;
  transform: scale(0.91);
}

.hero-slider__slide:nth-child(5) {
  z-index: 6;
  bottom: -60px;
  transform-origin: bottom;
  transform: scale(0.88);
}

.hero-slider__slide:nth-child(6) {
  z-index: 5;
  bottom: -75px;
  transform-origin: bottom;
  transform: scale(0.85);
}

.hero-slider__slide:nth-child(7) {
  z-index: 4;
  bottom: -90px;
  transform-origin: bottom;
  transform: scale(0.82);
}

.hero-slider__slide:nth-child(8) {
  z-index: 3;
  bottom: -105px;
  transform-origin: bottom;
  transform: scale(0.79);
}

.hero-slider__slide:nth-child(9) {
  z-index: 2;
  bottom: -120px;
  transform-origin: bottom;
  transform: scale(0.76);
}

.hero-slider__slide:nth-child(10) {
